* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 62.5%;
}

body {
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

main {
  width: 400px;
  height: 400px;
  position: relative;
  overflow: hidden;
}

img {
  width: 100%;
  transition-duration: 1s;
}

.caption {
  opacity: 0;
  color: #fff;
  width: 100%;
  height: 100%;
  background-color: #0000007f;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  transition-duration: 1s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

main:hover div {
  opacity: 1;
}

main:hover img {
  transform: rotate(15deg)scale(1.2);
}

