* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
:root {
  font-size: 62.5%;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}

main {
  position: relative;
  width: 400px;
  height: 400px;
  overflow: hidden;
}

img {
  width: 100%;
  transition-duration: 1s;
}

.caption {
  font-size: 2rem;
  opacity: 0;
  display: flex;
  color: white;
  position: absolute;
  top: 0;
  left: 0;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.499);
  width: 100%;
  height: 100%;
  transition-duration: 1s;
}

main:hover div {
  opacity: 1;
}

main:hover img {
  transform: rotate(15deg) scale(1.2);
}
